import moment from 'moment'
import _ from 'lodash'



export const _setApi = (uri) => (
    uri = 'https://cryptohot.exchange/api'
)

//key login
export const _setKey = (val) => (
    val = 'LO9!N%2~0-Cl!3nt/$-cRY~pt-0'
)
export const _setCrypt = (val) => (
    val = 'Cr!pt0/-$3eCur%R/3*~s$19n!n'
)

export const _setPrices = (value) => {
    let val = (value/1).toFixed().replace('.', '')
    return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
}

export const _setTotal = val => {
    return _.toString(val).replace(/\b(\d+)(\d{2})\b/, '$1.$2')
}

export const _unEqual = (val, nul) => {
    return (val !== nul)
}


//format date
export const _setHours = (val) => (
    moment(val).format('HH:mm:ss')
)


