import React from 'react'
import { Toast, ToastContainer } from 'react-bootstrap';
import { _setHours } from '../config'

const Notify = ({ onClose, onShow, texts }) => {
    return (
        <div>
       <ToastContainer position="top-end" className="p-3">
          <Toast onClose={onClose} show={onShow}
          delay={3000} autohide>
          <Toast.Header>
            <strong className="me-auto">Pemberitahuan!</strong>
            <small className="text-muted">{_setHours(new Date())}</small>
          </Toast.Header>
            <Toast.Body><b>{texts}</b></Toast.Body>
          </Toast>
        </ToastContainer>
    </div>
    )
}
export {
  Notify
}
