import React, { useState } from 'react';
import _ from 'lodash'
import axios from 'axios'
import { _setApi, Notify, _setKey, _setCrypt } from '../config'
import { useNavigate } from 'react-router-dom';
import CryptoJS from "crypto-js"


export default function SignIns() {
  const route = useNavigate()
  const [uname, setUname] = useState('')
  const [ulock, setULock] = useState('')
  const [state, setState] = useState({ text: '', show: false });

  const _setAuthenticationClient = async () => {
    try{
      if (_.isEmpty(uname) || _.isEmpty(ulock))
        return setState({text: `Form is required !.`, show: true})
      const items = new FormData()
      items.append('username', uname)
      items.append('password', ulock)

      let results = await axios.post(`${_setApi()}/login.php`, items, {
          headers: {
          'Content-Type': 'multipart/form-data'
          }
        })
        if (_.isEqual(_.toNumber(results.data.status), 1)) {
          let encrypt = CryptoJS.AES.encrypt(JSON.stringify(results.data.data), _setCrypt())
          localStorage.setItem(_setKey(), encrypt)
          setState({ show: true, text: results.data.message })
          setTimeout(() => route('/',  { replace: true },
            {state: { reload: true }}), 1850)
          // setTimeout(() => route(0), 1800)
        }else {
          setState({show: true, text: results.data.message})
        }
    }catch (e){
      setState({show: true, text: e})
    }
  }

    return (
      <div className="vh-100 d-flex justify-content-center">
      <Notify onClose={() => setState({ show: false })} onShow={state.show} texts={state.text} />
        <div className="form-access my-auto">
          <form>
            <span>Sign In</span>
            <div className="form-group">
              <input type="text" className="form-control"
                placeholder="Email"
                value={uname}
                onChange={e => setUname(e.target.value)}/>
            </div>
            <div className="form-group">
              <input type="password" className="form-control" placeholder="Password"
              value={ulock}
              onChange={e => setULock(e.target.value)}/>
            </div>
            <div className="text-right">
              <a href="reset-password-dark.html">Forgot Password?</a>
            </div>
            <div className="custom-control custom-checkbox">
              <input type="checkbox" className="custom-control-input" id="form-checkbox" />
              <label className="custom-control-label" htmlFor="form-checkbox">Remember me</label>
            </div>
            <button type="button" className="btn btn-primary"
              onClick={_setAuthenticationClient}>Sign In</button>
          </form>
          <h2>Don't have an account?
            <a href="signup-dark.html">Sign up here</a>
          </h2>
        </div>
      </div>
    )
}
