import React, { useState, useEffect } from 'react';

import { Link } from "react-router-dom";
import _ from 'lodash'
import {
  Container, Nav, Navbar, NavItem, NavDropdown, Button
} from 'react-bootstrap';
import CryptoJS from "crypto-js"

import './assets/css/style.css'
import './assets/css/ionicons.min.css'

import light from './assets/img/logo-light.svg'

import { _setKey, _setCrypt } from '../config'

export default function Header() {
  const [profile, setProfile] = useState([]);
  const [show, setShow] = useState(false);

  const _onSetRetrieveStorage = () => {
    let data = localStorage.getItem(_setKey())
    if (_.isNull(data)) return setShow(false)
      setShow(true)
      const bytes  = CryptoJS.AES.decrypt(data, _setCrypt())
      const originalText = bytes.toString(CryptoJS.enc.Utf8)
      setProfile(JSON.parse(originalText))
  }

   useEffect(() => {
    _onSetRetrieveStorage()
  }, [])

  return (
    <div >
   {/*<Navbar collapseOnSelect expand="lg"
    style={{ backgroundColor: '#131722' }}>*/}
    <Navbar collapseOnSelect expand="lg" variant="dark"
      style={styles.txtNavbar}>
        <Navbar.Brand >
         <Link style={styles.txtDw} to="/">
            <img src={light} alt="logo" style={styles.navImage}/>
         </Link>
        </Navbar.Brand>
      <Container style={{ padding: 6, paddingTop: 12 }} >
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
           <NavItem style={styles.txtMenu} >
            <Link style={styles.txtDw} to="/exchange">
              Exchange
            </Link>
           </NavItem>
           <NavItem style={styles.txtMenu}>
            <Link style={styles.txtDw} to="/markets">
              Markets
            </Link>
           </NavItem>
            <NavDropdown  title={
              <span className="my-auto" style={{ color: '#fff'}}
               onClick={() => alert('Masih dalam pengembangan.')}>
                Eaning
              </span>
            }
              style={styles.txtTitle}>
              <NavDropdown.Item>
                <Link style={styles.txtDw} to="/exchange-fluid">
                  Stake<br />
                 <span style={{ fontSize: 11 }}>
                  - Stake Crypto Get Rewards
                  </span>
                </Link>
                </NavDropdown.Item>
            </NavDropdown>
             <NavItem style={styles.txtMenu}>
            <Link style={styles.txtDw} to="/markets">
              Launchpad
            </Link>
           </NavItem>
          </Nav>

        </Navbar.Collapse>
        {
            show ?
            <div>
            <NavDropdown title={
              <span className="my-auto" style={{ color: '#fff'}}>
                Wallet
              </span>
            }
              style={styles.txtTitle}>
              <NavDropdown.Item>
                <Link style={styles.txtDw} to="/exchange-fluid">
                  My Fund
                </Link>
                </NavDropdown.Item>
              <NavDropdown.Item>
                <Link style={styles.txtDw} to="/exchange-fluid">
                  Deposit
                </Link>
                </NavDropdown.Item>
              <NavDropdown.Item>
                <Link style={styles.txtDw} to="/exchange-fluid">
                  Withdrawal
                </Link>
                </NavDropdown.Item>
            </NavDropdown>
            <NavDropdown  title={
              <span className="my-auto" style={{ color: '#fff'}}>
                {_.get(profile[0], 'username')}
              </span>
            }
              style={styles.txtTitle}>
              <NavDropdown.Item>
                <Link style={styles.txtDw} to="/exchange-fluid">
                  Refferal
                </Link>
                </NavDropdown.Item>
              <NavDropdown.Item>
                <Link style={styles.txtDw} to="/exchange-fluid">
                  Address Management
                </Link>
                </NavDropdown.Item>
              <NavDropdown.Item>
                <Link style={styles.txtDw} to="/exchange-fluid">
                  Logout
                </Link>
                </NavDropdown.Item>
            </NavDropdown>
            </div> :
            <div>
            <Nav>
            <Button size="lg" variant="outline-primary">
            <Link style={styles.txtDw} to="/authentication">
            Sign In
            </Link>
            </Button>{' '}
            <Button  size="lg" variant="primary" style={{ marginLeft: 18}}>
            Get Started
            </Button>
            </Nav>
          </div>
          }
      </Container>

    </Navbar>
    </div>
  );
}

let styles = {
  txtNavbar: {
    backgroundColor: '#131722',
    borderBottom: '0.1px solid rgb(160,160,255)'
  },
  navImage: {
    height: 45,
    marginLeft: 20
  },
  txtDw: {
    fontSize: 16,
    color: '#fff',
    textDecoration: 'none'
  },
  txtTitle: {
    marginLeft: 25,
    fontSize: 18,
    fontWeight: 400,
    fontFamily: 'sans-serif'
  },
  txtMenu: {
    marginTop: 8,
    marginLeft: 25,
    fontSize: 18,
    fontWeight: 400,
    textDecoration: 'none',
    fontFamily: 'sans-serif'
  }
}
